* {
  box-sizing: border-box;
}

html, body {
  overflow: hidden;
  font-family: Helvetica,san-serif;
}

button {
  background-color : blue;
  color: white;
  border-radius: 3px;
  border: 2px solid blue;
  padding: .75rem 1rem;
  font-size: 1rem;
  cursor: pointer;
  box-shadow: 0 0 10px red;
  
}

.grid {
  box-sizing: content-box;
  padding: 0;
  border-top: 1px solid lightgray;
  border-right: 1px solid lightgray;
  display: flex;
  flex-wrap: wrap;
}

.grid-cell {
  border-left: 1px solid lightgray;
  border-bottom: 1px solid lightgray;
}

.grid-cell--rat {
    background-color: blue;
  }
  
.grid-cell--snake {
    background-color: orange;
  }

.snake-app {
  position: relative;
  margin: 10px auto;
  &:focus {
    outline: none;
  }
}

.snake-app__overlay {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: hsla(0, 1, 0, .7);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: black;
  font-size: 1.2rem;
}


.mb-1 {
  margin-bottom: 1rem;
}